import cartdHead from '@/components/cartdHead/cartdHead';
import {
	mapActions,
	mapGetters
} from "vuex";
export default {
	components: {
		cartdHead
	},
	data() {
		return {
			sumPrice: '0',
			headerPosition: "fixed",
			headerTop: null,
			statusTop: null,
			showHeader: true,
			selectedList: [],
			isAllselected: false,
			goodsList: [],
			//控制滑动效果
			theIndex: null,
			oldIndex: null,
			isStop: false
		}
	},
	onPageScroll(e) {
		this.headerPosition = e.scrollTop >= 0 ? "fixed" : "absolute";
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	async onShow() {
		this.userinfo = this.getUserInfos;
		this.goodsList = this.getGoodsList;
		await Promise.all((this.goodsList.map((item, index) => {
			return new Promise(async (resolve, reject) => {
				this.$api.getGoodsInfoById({
					goodsId: item.goodsId,
					isflash: item.isflash
				}, (res) => {
					if (res.code) {
						this.goodsList[index].selected = false;
						this.isAllselected = false
						this.selectedList = []
						this.sumPrice = 0
						this.$set(this.goodsList[index], 'goodsInfo', res.data)
					}
				})
			})
		})))
	},
	computed: {
		...mapGetters({
			getGoodsList: "getGoodsList",
			getNameGoodListNum: 'getNameGoodListNum',
			getUserInfos: 'getUserInfos',
			getisLogin: 'getisLogin'
		})
	},
	methods: {
		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];

			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) {
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) {
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},
		touchEnd(index, $event) {
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		//商品跳转
		toGoods(e) {
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsId=' + e.goodsId
			});
		},
		//跳转确认订单页面
		toConfirmation() {
			let tmpList = [];
			let len = this.goodsList.length;
			for (let i = 0; i < len; i++) {
				if (this.goodsList[i].selected) {
					tmpList.push(this.goodsList[i]);
				}
			}
			if (tmpList.length < 1) {
				uni.showToast({
					title: '请选择商品结算',
					icon: 'none'
				});
				return;
			}
			uni.setStorage({
				key: 'buylist',
				data: tmpList,
				success: () => {
					uni.navigateTo({
						url: '../confirmation/confirmation'
					})
				}
			})
		},
		//删除商品
		deleteGoods(id) {
			this.selectedList.splice(this.selectedList.indexOf(id), 1);
			this.delCardAction(id)
			this.sum();
			this.oldIndex = null;
			this.theIndex = null;
		},
		// 删除商品s
		deleteList() {
			let len = this.selectedList.length;
			while (this.selectedList.length > 0) {
				this.deleteGoods(this.selectedList[0]);
			}
			this.selectedList = [];
			this.isAllselected = this.selectedList.length == this.goodsList.length && this.goodsList.length > 0;
			this.sum();
		},
		// 选中商品
		selected(index) {
			this.goodsList[index].selected = this.goodsList[index].selected ? false : true;
			let i = this.selectedList.indexOf(this.goodsList[index].id);
			i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(this.goodsList[index].id);
			this.isAllselected = this.selectedList.length == this.goodsList.length;
			this.sum();
		},
		//全选
		allSelect() {
			let len = this.goodsList.length;
			let arr = [];
			for (let i = 0; i < len; i++) {
				this.goodsList[i].selected = this.isAllselected ? false : true;
				arr.push(this.goodsList[i].goodsId);
			}
			this.selectedList = this.isAllselected ? [] : arr;
			this.isAllselected = this.isAllselected || this.goodsList.length == 0 ? false : true;
			this.sum();
		},
		// 减少数量
		sub(index) {
			if (this.goodsList[index].count <= 1) {
				return;
			}
			this.goodsList[index].count--;
			this.sum();
		},
		// 增加数量
		add(index) {
			this.goodsList[index].count++;
			this.sum();
		},
		// 合计
		sum(e, index) {
			this.sumPrice = 0;
			let len = this.goodsList.length;
			for (let i = 0; i < len; i++) {
				if (this.goodsList[i].selected) {
					if (e && i == index) {
						this.sumPrice = this.sumPrice + (e.detail.value * this.goodsList[i].price);
					} else {
						this.sumPrice = this.sumPrice + (this.goodsList[i].count * Number(this.goodsList[i].goodsInfo.goodsSale));
					}
				}
			}
			this.sumPrice = this.sumPrice.toFixed(2);
		},
		discard() {
			//丢弃
		},
		...mapActions({
			addCardAction: 'addCardAction',
			setUserInfoAction: 'setUserInfoAction',
			delCardAction: 'delCardAction'
		})
	}
}
